import React from "react";
import {
  setActiveComponent,
  setCandidates,
  toggleMask,
  changeMode
} from "../actions";
import { connect } from "react-redux";
import { selectCandidates } from "../utils";
import "antd/dist/antd.css";
import { Slider, Carousel } from "antd";

import "jquery/dist/jquery.min.js";
import "bootstrap/dist/js/bootstrap";

// the welcome scene containing a brief introduction and a table to obtain the user's input
class Welcome extends React.Component {
  constructor(props) {
    super(props);
    this.inputs ={};
    this.props.attributes.forEach(([attr, config]) => {
      this.inputs[attr] = [React.createRef(), React.createRef()];
    });
    this.inputs.maxPoints = React.createRef();
  
  }

  handleStart = () => {
    const ranges = [];
    const mask = [];
    for (let i = 0; i < this.props.attributes.length; ++i) {
      const [attr, config] = this.props.attributes[i];
      const range = [0, config.high];
      if (this.props.mask[attr]) {    
          const lowbound = this.inputs[attr][0].current.state.bounds[0];
          const upbound = this.inputs[attr][0].current.state.bounds[1];
          if (lowbound === upbound) {
            alert(`upper bound should be  large than lower bound`);
            return;
          
          }
          else {           
            range[1] = parseInt(upbound);
            range[0] = parseInt(lowbound);
          }
        
      }
      ranges.push(range);
      mask.push(this.props.mask[attr]);
    }
    let maxPoints;
    const str = this.inputs.maxPoints.current.state.value;
		maxPoints = parseInt(str);	
    const candidates = selectCandidates(
      this.props.points,
      ranges,
      mask,
      maxPoints
    );
    if (candidates.length === 0) {
      alert("No matching players, try larger ranges");
      return;
    }
    this.props.startAlgorithm(candidates);
  };

  handleModeChange = event => {
    this.props.changeMode(event.target.value);
  };

  render() {
    const trs = this.props.attributes.map(([attr, config]) => {
     // const disabled = this.props.mask[attr] === 0;
      const { low, high } = config;
      return (
        <tr key={attr}>
          <td className="align-middle">{attr}</td>
          <td colSpan="2">
            <div id ="container">
                 <Slider 
                 range                 
                 min={low}
                 max={high}             
                  tooltipVisible
                  defaultValue={[low,(low+high)/2]}
                  ref = {this.inputs[attr][0]}
                 />
            </div>
          </td>   
        </tr>
      );
    });
    trs.push(
      <tr key="other">
        <td className="align-top font-weight-bold">Max No. of Players</td>
        <td style={{textAlign:"center"}} colSpan="2">
            <div id ="container">
                  <Slider min={3000}
                 max={20000}
                 defaultValue={10000} tooltipVisible
                 ref={this.inputs.maxPoints}
                 style={{color:"#BA55D3"}}
                 />
            </div>
        </td>       
      </tr>
    );
    trs.push(
      <tr>
        <td>
           <label className="mr-4 col-form-label font-weight-bold">Method</label>
       </td>
        <td>
         <input           
              type="radio"
              name="inlineRadioOptions"
              id="simplex"
              value="simplex"
              checked={this.props.mode === "simplex"}
              onChange={this.handleModeChange}
          />
          <label className="form-check-label" htmlFor="simplex" style={{marginLeft:"2px"}}>
              Sorting-Simplex
          </label>
      </td>
        <td>
        <input
              type="radio"
              name="inlineRadioOptions"
              id="random"
              value="random"
              checked={this.props.mode === "random"}
              onChange={this.handleModeChange}
          />
          <label className="form-check-label" htmlFor="random" style={{marginLeft:"2px"}}>
             Sorting-Random
          </label>
      </td>
     </tr>
    );
    return (
      <div className="text-center m-auto" style={{ maxWidth: "55rem" }}>
        <h1>Identify your ideal player!</h1>
        <Carousel autoplay style={{background:"#87CDFA",borderRadius:"10px"}}>
     
     <div>
         <h4>How to use the system</h4>
         <div style={{width:"850px",height:"260px"}}>
            <img src={require('../Image/welcome-inc.png')} style={{marginLeft:"1%",height:"90%"}} alt="img1" />
         </div>           
       </div>
       <div>
          <h4>How to sort displayed points</h4>
         <div style={{width:"850px",height:"260px"}}>
            <img src={require('../Image/sorting-inc.png')} style={{marginLeft:"5%",height:"90%"}} alt="img2"/>
         </div>           
       </div>
       <div>
          <h4>Result interface</h4>
         <div style={{width:"850px",height:"260px"}}>
            <img src={require('../Image/result-inc.png')} style={{marginLeft:"9%",height:"90%"}} alt="img3" />
         </div>           
       </div>
     </Carousel>
        <table className="table">
          <thead>
            <tr>
              <th style={{textAlign:"center",width:"30%"}}>Attribute</th>
              <th style={{textAlign:"left",width:"40%"}}>Min</th>
              <th style={{textAlign:"right",width:"40%"}}>Max</th>
            </tr>
          </thead>
          <tbody>
            {trs}
             
          </tbody>
        </table>
        <div>
          <button
            type="button"
            className="btn btn-primary"
            style={{ width: "8rem" }}
            onClick={this.handleStart}
          >
            Start
          </button>
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({ attributes, mask, points, mode }) => ({
  attributes,
  mask,
  points,
  mode
});

const mapDispatchToProps = dispatch => ({
  startAlgorithm: candidates => {
    dispatch(setCandidates(candidates));
    dispatch(setActiveComponent("Interaction"));
  },
  toggleMask: (attr, mask) => dispatch(toggleMask(attr, mask)),
  changeMode: mode => dispatch(changeMode(mode))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Welcome);


